import React from 'react';
import useDarkMode from 'use-dark-mode';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Resume from '../resume.pdf';
import { useMediaQuery } from 'react-responsive';

const useStyles = makeStyles({
  button_white: {
    color: 'white',
    boxShadow: '0 3px 5px 2px rgba(90, 90, 90, .3)',
    textTransform: 'none'
  },
  button_black: {
    color: 'black',
    boxShadow: '0 3px 5px 2px rgba(90, 90, 90, .3)',
    textTransform: 'none'
  },
});

const button_style = {
  width: '2vw',
  height: '6vw',
  fontSize: '1.5vw'
};

const button_style_1000 = {
  width: '150px',
  height: '35px',
  fontSize: '15px'
};

export default function BottomNav() {
  const darkMode = useDarkMode(false);
  const classes = useStyles();
  const sz1000 = useMediaQuery({
      query: '(min-device-width: 1000px)'
  });
  if (sz1000) {
    return (
      <div className="bottom-nav">
        <Button variant="outlined" className={(darkMode.value == true) ? classes.button_white :
        classes.button_black} style={button_style_1000}>
        CV 💼
        </Button> <div className="bottom-nav-divider"/>
        <Button variant="outlined" className={(darkMode.value == true) ? classes.button_white :
        classes.button_black} style={button_style_1000}>
          Projects 📝
        </Button> <div className="bottom-nav-divider"/>
        <Button variant="outlined" className={(darkMode.value == true) ? classes.button_white :
        classes.button_black} style={button_style_1000}>
          Blog 💻
        </Button>
      </div>
    );
  }
  else {
    return (
      <div className="bottom-nav">
        <Button variant="outlined" className={(darkMode.value == true) ? classes.button_white :
        classes.button_black} style={button_style}>
        CV 💼
        </Button> <div className="bottom-nav-divider"/>
        <Button variant="outlined" className={(darkMode.value == true) ? classes.button_white :
        classes.button_black} style={button_style}>
          Projects 📝
        </Button> <div className="bottom-nav-divider"/>
        <Button variant="outlined" className={(darkMode.value == true) ? classes.button_white :
        classes.button_black} style={button_style}>
          Blog 💻
        </Button>
      </div>
    );
  }
}
