import React from 'react';
import useDarkMode from 'use-dark-mode';
import GitHubIcon from '@material-ui/icons/GitHub';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import { useMediaQuery } from 'react-responsive';
import { Link } from '@material-ui/core';

const link_icon = {
  fontSize: "3vw",

};
const git_icon = {
  fontSize: "2.5vw",

};

const git_1000_plus = {
  fontSize: "23",
};

const link_1000_plus = {
  fontSize: "28",
};

function ShowDarkLogos() {
  const sz1000 = useMediaQuery({
      query: '(min-device-width: 1000px)'
  });
  if (sz1000) {
    return (
      <>
        <a href="https://www.linkedin.com/in/shervan-gheidi/">
          <LinkedInIcon color="secondary" style={link_1000_plus}/>
        </a>
        <a href="https://www.github.com/sgheidi">
          <GitHubIcon color="secondary" style={git_1000_plus}/>
        </a>
      </>
    );
  }
  else {
    return (
        <>
          <a href="https://www.linkedin.com/in/shervan-gheidi/">
            <LinkedInIcon color="secondary" style={link_icon}/>
          </a>
          <a href="https://www.github.com/sgheidi">
            <GitHubIcon color="secondary" style={git_icon}/>
          </a>
        </>
    );
  }
}

function ShowLightLogos() {
  const sz1000 = useMediaQuery({
      query: '(min-device-width: 1000px)'
  });
  if (sz1000) {
    return (
      <>
        <a href="https://www.linkedin.com/in/shervan-gheidi/">
          <LinkedInIcon color="action" style={link_1000_plus}/>
        </a>
        <a href="https://www.github.com/sgheidi">
          <GitHubIcon color="action" style={git_1000_plus}/>
        </a>
      </>
    );
  }
  else {
    return (
        <>
          <a href="https://www.linkedin.com/in/shervan-gheidi/">
            <LinkedInIcon color="action" style={link_icon}/>
          </a>
          <a href="https://www.github.com/sgheidi">
            <GitHubIcon color="action" style={git_icon}/>
          </a>
        </>
    );
  }
}

export default function SocialMedia() {
  const darkMode = useDarkMode(false);
  if (darkMode.value == true) {
    return <ShowDarkLogos />
  }
  else if (darkMode.value == false) {
    return <ShowLightLogos />
  }
}
